<template>
  <div>
    <div class="container">
      <div class="wrapper" v-show="botton_show">
        <div
          class="card"
          v-for="item in cardData"
          :key="item.value"
          @click="isDialog(item.label, item.value)"
        >
          <p>{{ item.title }}</p>
        </div>

        <div class="card" @click="goToResources()">
          <p>前端资源页</p>
        </div>
        <div class="card" @click="goToMarkdown()">
          <p>Markdown文件</p>
        </div>
      </div>
      <el-dialog :title="windowTitle" :visible.sync="dialogVisible" width="60%">
        <div v-if="resources === 'douban'">豆瓣主页正在开发中</div>
        <div v-else-if="resources === 'markdown'">
          markdown--button主页正在开发中
        </div>
      </el-dialog>
      <div class="resourcesPage" v-show="!botton_show">
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  components: {},
  props: {},
  data() {
    return {
      botton_show: true,
      dialogVisible: false,
      windowTitle: "",
      resources: "",
      cardData: [
        {
          title: "我的豆瓣",
          label: "豆瓣",
          value: "douban",
        },
        {
          title: "前端资源页",
          label: "前端资源",
          value: "font-end",
        },
        {
          title: "Markdown文件",
          label: "Markdown文件",
          value: "markdown",
        },
      ],
    };
  },
  computed: {},
  watch: {},
  created() {},
  mounted() {},
  methods: {
    goToResources() {
      this.$router.push({ path: "resources" });
      this.botton_show = false;
    },
    goToMarkdown() {
      this.$router.push({ path: "/markdown" });
    },
    isDialog(label, value) {
      this.dialogVisible = true;
      this.windowTitle = label;
      this.resources = value;
    },
  },
};
</script>

